/* menu nav item */
.taxonomy-nav {
  position: relative;
  z-index: 1;
}

.taxonomy-nav .menu-item {
  padding: 0.75em 2em;
  margin-bottom: 0;
  font-size: 1.2em;
  font-weight: 600;
}

.taxonomy-nav .menu-item .dropdown-toggle {
  position: relative;
  white-space: nowrap;
}

.taxonomy-nav .menu-item .dropdown-toggle:after {
  display: inline-block;
  content: "";
  margin-left: 0.5em;
  border-top: 0.3em solid;
  border-right: 0.3em solid transparent;
  border-bottom: 0;
  border-left: 0.3em solid transparent;
}

@media all and (max-width: 767px) {
  .taxonomy-nav .menu-item {
    width: 100%;
    text-align: center;
  }
}

.taxonomy-nav .menu-item:hover > a {
  color: orange;
}

.taxonomy-nav .menu-item:last-of-type .dropdown-menu {
  right: 0;
}

.taxonomy-nav .menu-item:last-of-type .dropdown-menu:before {
  left: initial;
  right: 0;
}

.taxonomy-nav .dropdown-menu {
  display: flex !important;
  flex-direction: row;
  margin: 0;
  padding: 0;
  left: initial;
  float: none;
  border: none;
  margin-top: -10px;
  font-weight: 500;
  z-index: -1;
  opacity: 0;
  transition: 0.4s all, 0s 0.75s padding;
  max-height: 0;
  box-shadow: 3px 8px 10px 0 rgba(0, 0, 0, 0.35);
  overflow: hidden;
  font-family: "Poppins", sans-serif;
  border-radius: 20px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.taxonomy-nav .dropdown-menu.sub {
  left: calc(100% + 1.25em);
  top: -1.25em;
  border-bottom-left-radius: 0;
}

@media all and (max-width: 767px) {
  .taxonomy-nav .dropdown-menu {
    width: 100%;
    left: 0;
    text-align: center;
    flex-wrap: wrap;
  }
}

.taxonomy-nav .dropdown-menu:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 2px;
  opacity: 0;
  background: #548bff;
  transition: all 0.25s, 0.25s 0.125s width;
}

.taxonomy-nav .dropdown-menu li {
  margin-bottom: 0;
  width: 100%;
}

.taxonomy-nav .dropdown-menu li a {
  font-size: 15px;
  color: #548bff;
  opacity: 0.7;
  transition: 0.2s;
  padding: 0.85em 0.25em;
}

.taxonomy-nav .dropdown-menu li a:hover {
  opacity: 1;
}

.taxonomy-nav .dropdown-menu li a:first-of-type {
  padding-top: 0.125rem;
}

.taxonomy-nav .dropdown:hover > .dropdown-menu {
  opacity: 1;
  z-index: 1;
  padding: 1.25em 1.25em 0.65em;
  transition: 0.4s all, 0s padding;
  margin-top: 0px;
  max-height: 350px;
  overflow: visible;
}

@media all and (max-width: 767px) {
  .taxonomy-nav .dropdown:hover > .dropdown-menu {
    max-height: initial;
  }
}

.taxonomy-nav .dropdown:hover > .dropdown-menu:before {
  width: 100%;
  opacity: 1;
}

